.main {
    position: relative;
    width: 100%;
    background: url('../img/bg-main.png') center (center/cover) no-repeat;
    background-color: rgba(12, 13, 27, 1);
    padding: 37px 0px 135px 0px;
    z-index: 1;

    &__blur-left {
        position: absolute;
        top: 0;
        left: 0;
    }

    &__blur-right {
        position: absolute;
        top: 0;
        right: 0;
    }

    &__wrapper-logo{
        display: block;
        position: relative;
        margin: 0 auto;
        width: 310px;
        height: 200px;
        cursor: pointer;
        z-index: 9999;
        border: 1px solid rgba(255, 0, 0, 0);
    }

    &__logo-shadow{
        position: absolute;
        z-index: -10;
    }

    &__logo {
        display: block;
        margin: 0 auto;
        margin-top: 30px;
        cursor: pointer;
    }

    &__wrapper {
    }
}
.deposit {
    position: relative;
    width: 422px;
    height: 146px;
    margin: 0 auto;
    margin-top: 113px;
    padding-top: 6px;
    background: #660000;
    box-shadow: 0px 40px 25px 7px rgba(0, 0, 0, 0.2);

    &__wrapper-left {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: absolute;
        top: 34px;
        left: -130px;
        z-index: -5;
    }
    &__wrapper-right{
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 34px;
        right: -90px;
        z-index: -5;
    }

    &__text {
        font-style: normal;
        font-weight: 900;
        font-size: 100px;
        line-height: 100%;
        text-align: center;
        color: #EAA917;
        span{
            display: block;
            font-weight: 600;
            font-size: 20px;
            line-height: 24px;
            color: #A9A9A9;
        }
    }

    &__bottom-text{
        margin-top: 35px;
        font-style: normal;
        font-weight: 600;
        font-size: 28px;
        line-height: 34px;
        text-align: center;
        color: #EAA917;
        span{
            color: #FFFFFF;
        }
    }

}
.footer {
    position: relative;
    min-height: 290px;
    background: #0C0D1B;

    &__blur-right{
        position: absolute;
        top: -500px;
        right: 0;
        z-index: 10;
    }
    &__blur-center{
        position: absolute;
        top: -200px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 10;
    }

    &__wrapper{
        display: flex;
        justify-content: space-between;
        
    }

    &__wrapper-items {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        z-index: 20;
    }

    &__img {
        width: 70px;
        height: 70px;
        margin-right: 40px;
    }

    &__text {
        margin-top: 40px;
        font-style: normal;
        font-weight: 600;
        font-size: 28px;
        line-height: 34px;
        text-align: center;
        color: #FFFFFF;
        span{
            margin-top: 15px;
            font-size: 20px;
            line-height: 24px;
            color: #A9A9A9;
        }
    }
}
@media (max-width: 1157px) {
    .footer{
        &__blur-center{
            top: 0;
            bottom: 0;
        }
    }
}
@media (max-width: 890px) {
    .footer{
        &__wrapper{
            flex-direction: column;
        }
        &__wrapper-items{
            margin-bottom: 50px;
        }
        &__img{
            margin: 0;
        }
        &__text{
            margin-top: 20px;
        }
        &__blur-right{
            top: -200px;
        }

        &__blur-center{
            top: 491px;
        }

    }
    .deposit{
        width: 190px;
        height: 80px;
        margin-top: 100px;
        &__text{
            font-size: 40px;
            span{
                font-size: 11px;
            }
        }
        &__t-left{
            width: 90px;
        }
        &__b-left{
            width: 90px;
        }
        &__t-right{
            width: 90px;
        }
        &__b-right{
            width: 90px;
        }
        &__wrapper-left{
            top: 24px;
            left: -57px;
        }
        &__wrapper-right{
            top: 24px;
            right: -47px;
        }
        &__bottom-text{
            font-size: 19px;
        }
    }
}